
.messages {
    height: auto;
}


.message + .message {
    margin-top: 20px;
}

.message span{
    display: inline-block;
    border: 1px solid  #9192ab;
    padding: 10px;
    box-sizing: border-box;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    position: relative;
}

.message span::before, .message span::after  {
    content: '';
    width: 15px;
    height: 1px;
    background-color: #9192ab;
    position: absolute;
    left: -15px;
    top: 10px;
    transform: rotate(-40deg);
    transform-origin: left;
}

.message span::after {
    transform: rotate(40deg);
    transform-origin: left;
    width: 17px;
}


