.header{
    grid-area: header;
    display: flex;
    align-items: flex-start;
    padding: 0 20px 10px 20px;
    margin-bottom: 10px;
    background-color: #F2F2F2;
    position: relative;
    text-align: center;
    height: 60px;
    -webkit-border-radius:  0 0 10px 10px;
    -moz-border-radius:  0 0 10px 10px;
    border-radius:  0 0 10px 10px;
}

.logo {
    color: #636363;
    font-size: 40px;
    margin: 0;
}

.logo  span {
    color: #78BBE6;
}

.login  {
    position: absolute;
    right: 20px;
    top: 0;
}

.login a {
    color: #8789A4;
    text-decoration: none;
}

.buttonLogout {
    position: absolute;
    top: 20px;
    right: 18px;
    width: 80px;
}


.login::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background: #8789A4;
    bottom: 0;
    left: 0;
    transform: scale(0);
    transition: all 0.4s;
}

.login:hover::after {
    transform: scale(1);
}

@media (max-width: 767px) {
    .hamburger  {
        display: block;
    }

    .header {
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 90%;
        margin: 0 auto;
    }

    .logo {
        position: absolute;
        left: 40%;
        transform: translateX(-50%);
        display: block;
        font-size: 35px;
    }

    .login {
        top: 4px;
        right: 11px;
    }

}

@media (max-width: 767px) {
    .logo {
        font-size: 30px;
    }
    .buttonLogout {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        font-size: 11px;
    }
}





