.userPhoto {
    width: 70px;
    height: 70px;
    padding: 30px 0;
}

.userPhoto img {
    max-width: 100%;
}

.userBlock + .userBlock{
    margin-top: 5px;
}

.page {
    cursor: pointer;
}

.page.active {
    font-weight: bold;
}

.button[disabled] {
    opacity: 0.3;
    pointer-events: none;
}

.button[disabled].after {
    opacity: 0.3;
}

@media (max-width: 767px) {
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .userPhoto  {
        padding: 30px 65px;
    }
}






