.content {
    padding: 40px 30px;
    margin-left: 10px;
    background-color: #F2F2F2;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.name + .name {
    margin-top: 40px;
}

.name a, .message a {
    text-decoration: none;
    color: #9192ab;
}


.message + .message {
    margin-top: 40px;
}

@media (max-width: 767px)  {
    .content {
        grid-template-columns: 5fr 7fr;
    }
}


