
body {
    padding: 0 15px;
    box-sizing: border-box;
}

textarea, input {
    box-sizing: border-box;
}

.button {
    padding: 10px 15px;
    background: #78bbe6;
    color: #FFF;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 10px;
    width: 100px;
    margin-top: 10px;
    border: none;
    outline: none;
    cursor: pointer;
    position: relative;
    z-index: 1;
    text-transform: uppercase;
    font-size: 12px;
}

.button::after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 0;
    height: 100%;
    background-color: #1E90FF;
    z-index: -1;
    transition: all 0.4s;
    border-radius: 10px;
}

.button:hover::after {
    width: 100%;
    left: 0;
}

.content {
    padding: 30px 20px;
    grid-area: content;
    margin-left: 10px;
    background-color: #F2F2F2;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 10px;
}

.appWrapper {
    color: #8789A4;
    background-color: #DCDCDC;
    box-sizing: border-box;
    overflow: hidden;
    height: 100%;
}

.container {
    max-width: 1300px;
    display: grid;
    grid-template-areas: "header header" "navbar content";
    grid-template-columns: 2fr 10fr;
    margin: 0 auto;
}

textarea, input {
    padding: 10px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
}

textarea:focus, input:focus {
    outline: none;
    border: 1px solid #ccc;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
}

input {
    border: 1px solid #ccc;
    padding: 5px 10px;
}

input, textarea {
    color: #757575;
}


form {
    padding: 0 10px;
}

.formInput + .formInput{
    margin-top: 10px;
    margin-bottom: 10px;
    display: inline-block;
}

.formInput input {
    height: 25px;
}

.errorMessage {
    color: darkred;
    border: 1px solid darkred;
    position: absolute;
    width: 500px;
    height: 500px;
    top: calc(50% - 250px);
    left: calc(50% - 250px);
    line-height: 500px;
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    z-index: 2;
}

.statusMessage {
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 2;
}

@media (max-width: 767px) {
    body {
        background-color: #DCDCDC;
    }
    .container {
        background-color: #F2F2F2;
        grid-template-areas: "header header" "content content";
        grid-template-rows: 1fr;
        grid-template-columns: 1fr;
        justify-items: center;
    }
}

@media (max-width: 520px) {
    .errorMessage {
        width: 300px;
        height: 300px;
        font-size: 20px;
        line-height: 300px;
        top: calc(50% - 150px);
        left: calc(50% - 150px);
    }

    .statusMessage {
        line-height: 1.5;
    }
}


