.dialogsContent {
    display: grid;
    grid-template-columns: 4fr 10fr;
    grid-template-rows: 1fr 1fr;
    padding-right: 50px;
}

.name + .name {
    margin-top: 40px;
}

.name a, .message a {
    text-decoration: none;
    color: #9192ab;
}

.addMessage {
    margin-top: 25px;
}

.textarea {
    width: 100%;
    margin: 0 auto;
    display: block;
    height: 60px;
    resize: none;
    padding: 10px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    border: 1px solid #9192AB;
}

.textarea:focus {
    outline: none;
    border: 1px solid #ccc;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
}

.textarea::placeholder {
    font-size: 16px;
    color: #9192AB;
}

.buttonWrap {
    display: flex;
    justify-content: flex-end;
    padding-right: 40px;
}

.button {
    margin: 0 auto;
    margin-top: 20px;
}

@media (max-width: 767px) {
    .dialogsContent {
        padding-top: 50px;
        grid-template-columns: 1fr 1fr;
        padding-right: 5px
    }
    
    .message {
        margin-left: 5px;
    }
}


