.background {
    margin-bottom: 30px;
}
.image {
    max-width: 100%;
    display: block;
    margin: 0 auto;
}

.description {
    display: flex;
    flex-direction: column;
    margin-left: 52px;
}

.avatar {
    width: 150px;
    height: 150px;
    padding: 0 20px;
}

.infoAvatar {
    padding: 0;
    margin-bottom: 10px;
    position: relative;
}

.infoAvatar:last-child {
    margin-bottom: 0;
}
.descriptionBlock + .descriptionBlock {
    margin-top: 5px;
}

.avatar img {
    object-fit: cover;
    display: block;
    margin: 0 auto;
}


.avatarImg {
    width: 100%;
    height: 100%;
}
h2 {
    margin: 10px;
}

.textarea {
    width: 90%;
    margin: 0 auto;
    display: block;
    height: 100px;
    resize: none;
    padding: 10px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
}

.textarea:focus {
    outline: none;
    border: 1px solid #ccc;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
}

.textarea::placeholder {
    font-size: 16px;
}
.posts {
    padding: 20px;
}

.buttonWrap {
    display: flex;
    justify-content: flex-end;
    padding-right: 40px;
}

.post {
    margin-top: 30px;
    display: flex;
    align-items: center;
}

.postImg {
    width: 50px;
    height: 50px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 20px;
}

.postText {
    flex: 1 1 80%;
}

.likes {
    margin-right: 50px;
    white-space: nowrap;
}

.postImg img {
    height: 100%;
    width: 100%;
}

.statusText {
    cursor: pointer;
    margin-top: 50px;
}

.selectFile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1
}

.fileButton {
    display: inline-block;
    margin: 20px 0;
    text-align: center;
    padding: 10px 5px;
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translateX(calc(100% + 10px));
    bottom: -20px;
}

.editBtn {
    margin-bottom: 10px;
}

.infoInput {
    margin-top: 10px;
}

.infoTextarea {
    width: 200px !important;
    resize: vertical;
}

.infoInput, .infoTextarea {
    margin-bottom: 5px;
}

.infoTextarea {
    margin-top: 5px;
}

.infoInput::placeholder, .infoTextarea::placeholder {
    color: #cccccc;
}

.loginError {
    color: darkred;
    border: 1px solid darkred;
    padding: 10px;
    margin: 10px 0;
}

@media (max-width: 767px)  {
    .content {
        margin: 0;
        width: 90%;
        top: 80px;
        left: 15px;
    }

    .background {
        height: 150px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        overflow: hidden;
    }
    .image {
        max-width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .likes {
        margin-right: 0;
    }
    .posts {
        padding: 0;
    }
    .buttonWrap {
        padding: 0;
    }
    .button {
        margin: 0 auto;
        margin-top: 10px;
    }

    .postImg {
        flex: 0 0 50px;
    }

    .description {
        margin-left: 0;
    }

    .profileWrapper + .profileWrapper {
        margin-top: 10px;
    }
}

@media (max-width: 380px) {
    .fileButton {
        width: 75px;
        font-size: 11px;
    }
}

