.pagesRow {
    display: flex;
}

.pages {
    white-space: nowrap;
}

.pages span {
    display: inline-block;
    width: 25px;
    height: 25px;
    padding: 5px;
    background-color: #ffffff;
    color: #78BBE6;
    cursor: pointer;
    border: 1px solid #F2F2F2;
    text-align: center;
    line-height: 25px;
}

.pages span.active {
    font-weight: bold;
}

.pages span:hover{
    width: 27px;
    height: 27px;
    transform: translateY(-1px);
}

.pageButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    padding: 5px;
    background-color: #fff;
    border: none;
    margin-top: 1px;
    cursor: pointer;
    outline: none;
}
.pageButton:hover {
    width: 37px;
    height: 37px;
    transform: translateY(-1px);
}

.pageButton:active {
    border: none;
    outline: none;
}

.pageButton img {
    width: 18px;
}

@media (max-width: 500px) {
    .pagesRow {
        position: absolute;
        transform: rotate(90deg);
        left: -175px;
        top: calc(50% + 100px);
    }
    .pages span {
        transform: rotate(-90deg);
    }
}


