.navbar{
    grid-area: navbar;
    padding: 25px 20px;
    margin-right: 10px;
    background-color: #F2F2F2;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 10px;
    min-height: 100vh;
}

.navbar.active {
    transform: translateX(0);
}

.navMenu {
    margin: 0;
    padding: 0;
    list-style: none;
}

.item {
    padding: 5px;
    width: 100%;
    margin-bottom: 5px;
}
.item:last-child {
    margin-bottom: 0;
}

.item:hover .navLink {
    color: #fff;
}

.navLink {
    display: inline-block;
    color: #8789A4;
    text-decoration: none;
    font-size: 18px;
    padding-left: 10px;
    line-height: 2;
    width: 100%;
}

.navLink.active {
    color: #fff;
    background-color: #78BBE6;
    border-radius: 30px;
}

.navLink:hover {
    background-color: #78BBE6;
    -webkit-border-radius: 30%;
    -moz-border-radius: 30%;
    border-radius: 30px;
}

.hamburger {
    width: 25px;
    height: 20px;
    position: absolute;
    cursor: pointer;
    display: none;
    top: 20px;
    left: 40px;
    z-index: 10;
}

.hamburger span{
    content: '';
    position: absolute;
    top: 9px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #636363;
    transition: opacity 0.5s;
    opacity: 1;
}

.hamburger::before {
    content: '';
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #636363;
    transition: all 0.4s;
}

.hamburger::after {
    content: '';
    position: absolute;
    top: 18px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #636363;
    transition: all 0.4s;
}


.hamburger.active span {
    opacity: 0;
    transition: opacity 0s;
}

.hamburger.active::before {
    transform: rotate(45deg) translateY(12.5px);
}

.hamburger.active::after {
    transform: rotate(-45deg) translateY(-12.5px);
}

@media (max-width: 767px) {
    .navbar {
        position: fixed;
        top: 0;
        left: 0;
        width: 90%;
        height: 100%;
        z-index: 5;
        transition: all 0.4s;
        transform: translateX(-200%);
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
    }

    .navMenu {
        padding-right: 20px;
        padding-top: 40px;;
    }

    .item + .item {
        margin-top: 20px;
    }

    .navLink {
        font-size: 20px;
    }
    .hamburger {
        display: block;
    }

}

